'use strict';

// MODULES //

import Complex128 from './../../../../float64/ctor';
import real from './../../../../float64/real';
import imag from './../../../../float64/imag';


// MAIN //

/**
* Adds two double-precision complex floating-point numbers.
*
* @param {Complex128} z1 - complex number
* @param {Complex128} z2 - complex number
* @returns {Complex128} result
*
* @example
* import Complex128 from '@stdlib/complex/float64/ctor';
* import real from '@stdlib/complex/float64/real';
* import imag from '@stdlib/complex/float64/imag';
*
* var z = new Complex128( 5.0, 3.0 );
* // returns <Complex128>
*
* var out = cadd( z, z );
* // returns <Complex128>
*
* var re = real( out );
* // returns 10.0
*
* var im = imag( out );
* // returns 6.0
*/
function cadd( z1, z2 ) {
	var re = real( z1 ) + real( z2 );
	var im = imag( z1 ) + imag( z2 );
	return new Complex128( re, im );
}


// EXPORTS //

export default cadd;
